.navbar {
    height: 8rem;
    padding: .4rem 0;
    transition: all .5s;
    -webkit-backface-visibility: hidden;
    -webkit-transform-style: preserve-3d;

    .navbar-brand .navbar-brand-img {
        height: 2.4rem;
        width: 2.4rem * 480 / 90;
        margin-top: 2rem;
        transition: all .5s ease-in-out;

        -webkit-backface-visibility: hidden;
        -webkit-transform-style: preserve-3d;

        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("/img/logo-white.png");
    }

    &.navbar-opaque {
        height: 3.2rem;
        background-color: transparentize($gray-lightest, .1);
        border-bottom: solid 0.0625rem $gray-lighter;
        .navbar-brand .navbar-brand-img {
            background-image: url("/img/logo.png");
            height: 1.8rem;
            width: 1.8rem * 480 / 90;
            margin-top: 0;
        }
    }

    &.preload {
        transition: none !important;
        .navbar-brand .navbar-brand-img { transition: none !important; }
    }
}

.navbar-toggler {
    color: $gray-light;
    outline: none;
}

